<template>
<div>
  <v-app id="login" class="indigo">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm6 md6 lg6>
            <v-card class="elevation-1 pa-3">
              <v-card-text style="text-align:center">
                <img :src="this.$apiUrl + 'images/qrcode/' + this.$route.params.qrcode + '.png'" alt=""><br>
                Terima kasih sudah bergabung bersama kami, silahkan scan qrcode ini di bagian pelayanan untuk memperoleh username dan password.
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
  </div>
</template>

<script>
export default {
}
</script>



